body{
  background-color: #1e2931;;
  color: #fff;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-style: normal;
  line-height: 1.5;
  margin: 0 auto;
  width: 90%;
}
.search{
  font-size: 1.5rem;
  margin: 1.5em auto 3em;
  max-width: 80%;

}
.search [type='search']{
  width: 100%;
  background-color: #fff;
  padding: 0.5em 2em;
  text-align: center;
}
.moviesWrapper{
  display: flex;
  flex-wrap: wrap;
  width: 90%;
  margin: auto;
}
.moivesCard{
  width: calc(16.6% - 27px);
  margin: 25px;
  box-shadow: 0 0 1em rgb(0 0 0 / 30%);
}
.movieImg{
  width: 100%;
  height: 200px;

}
